import { useEffect } from 'react';
import { differenceInDays } from 'date-fns';
import socket from './../../api/ws';
import styles from './TasksList.module.scss';

function TasksList ({ tasks, setTasks, userName }) {
  socket.on('updated', payload => {
    const newTasks = [...tasks];
    newTasks[newTasks.findIndex(t => t.id === payload.id)] = { ...payload };
    setTasks(newTasks);
  });

  return (
    <>
      {/* <section>
          <h2>Immediate</h2>
          <ul>
            {tasks
              .filter(
                t =>
                  t.deadline &&
                  differenceInDays(new Date(t.deadline), new Date()) <= 3
              )
              .map(t => (
                <li key={t.id}>
                  {t.id} {t.body} {t.deadline}{' '}
                </li>
              ))}
          </ul>
        </section> */}
      {/* <section>
          <h2>Terminable</h2>
          <ul>
            {tasks
              .filter(
                t => differenceInDays(new Date(t.deadline), new Date()) > 3
              )
              .map(t => (
                <li key={t.id}>
                  {t.id} {t.body} {t.deadline}
                </li>
              ))}
          </ul>
        </section> */}
      <section>
        <h2>Common</h2>
        <ul>
          {tasks
            // .filter(t => !t.deadline)
            .map(t => (
              <li key={t.id}>
                <input
                  type='checkbox'
                  checked={t.isDone}
                  onChange={({ target: { checked } }) => {
                    socket.emit('task:update', {
                      ...t,
                      isDone: checked,
                      implementerName: checked ? userName : undefined,
                    });
                  }}
                />
                {t.authorName} {t.id} {t.body} {t.deadline}
                {t.isDone && t.implementerName}
              </li>
            ))}
        </ul>
      </section>
    </>
  );
}

export default TasksList;
